*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
#designsingledescription{
  height: 30.5%;
}
#designsinglecomment{
  height: 30.5%;
}

.singledesign{
    display: flex;
    background-color: white;
}
.singledesign .sdesignimg{
    width: 60%;
    height: 94vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(194, 194, 194);
}
.singledesign .sdesignimg img{
    object-fit: contain;
    max-height: 64vh;
}
.singledesign .sdesigndetail{
    width: 40%;
}
.singledesign .sdesigndetail .sdesignhead{
    text-align: center;
    width: 100%;
    height: 14%;
    border-bottom: 0.3px solid  #cec8c8;
    padding-top: 10px;
   
}
.singledesign .sdesigndetail .sdesignhead img{
    border-radius: 50%;
    object-fit: cover;
}
.singledesign .sdesigndetail .sdesignhead h6{
    font-family: 'Times New Roman', Times, serif;
font-size: 16px;
font-weight: 400;

letter-spacing: 0.1em;
text-align: center;

}
.singledesign .sdesigndetail .sdescription1,.singledesign .sdesigndetail .scomments1 {
    height: 30%;
    overflow-y: scroll;
    border-bottom: 0.3px solid  #cec8c8;
    padding-top: 10px;
    padding-left: 10px;
}
.singledesign .sdesigndetail .sdescription1::-webkit-scrollbar {
    width: 6px;
    height: 6%;
  }
  
  .singledesign .sdesigndetail .sdescription1::-webkit-scrollbar-track {
    background-color: transparent;
    -webkit-border-radius: 10px;
    border-radius: 10px;
    height: 90%;
  }
  
  .singledesign .sdesigndetail .sdescription1::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    height: 10% !important;
    background: #6d6d6d;
  }
  .singledesign .sdesigndetail .scomments1::-webkit-scrollbar {
    width: 6px;
    height: 6%;
  }
  
  .singledesign .sdesigndetail .scomments1::-webkit-scrollbar-track {
    background-color: transparent;
    -webkit-border-radius: 10px;
    border-radius: 10px;
    height: 90%;
  }
  
  .singledesign .sdesigndetail .scomments1::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    height: 10% !important;
    background: #6d6d6d;
  }
.singledesign .sdesigndetail .scomments1 p{
    margin-bottom: 0;
}
.singledesign .sdesigndetail .scomments1 .spcomments{
    display: flex;
    height: 50px;  
    align-items: center;
  
}
.singledesign .sdesigndetail .scomments1 .spcomments img{
    border-radius: 50%;
    object-fit: contain;
}
.singledesign .sdesigndetail .scomments1 .spcomments h6{
    font-size: 14px;
    font-weight: 600;
    padding-left: 10px;
    margin-bottom: 0;
}
.singledesign .sdesigndetail .scomments1 .spcomments p{
    font-size: 14px;
    margin-bottom: 0;
    padding-left: 20px;
}
.singledesign .sdesigndetail .commentinput {
    height: 5%;
    border-bottom: 0.3px solid  #cec8c8;
    display: flex;
    padding-left: 20px;
    align-items: center;
}
.singledesign .sdesigndetail .commentinput  input{
  background-color: transparent;
  border: none;
  outline: none;
  width: 100%;
}
.singledesign .sdesigndetail .publisheddetails{
    display: flex;
    height: 6%;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
    align-items: center;
    border-bottom: 0.3px solid  #cec8c8;
}
.singledesign .sdesigndetail .publisheddetails .pubdate h5{
    letter-spacing: 0.1em;  
    color: #000000;
    font-family: Calibri;
    font-style: normal;
    font-weight: 100;
    font-size: 14px;
    margin-bottom: 0;
}
.singledesign .sdesigndetail .cfoot{
    height: 14%;
    padding: 0 20px;
}
.singledesign .sdesigndetail .frow {
    height: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 5px;
}
.singledesign .sdesigndetail .frow .footicongroup{
  display: flex;
  align-items: center;
}
/* /* bookmark setting  */
.bookmarkicons:hover .bookmarktpop{
    display: flex;
    transition: 1s;
  }
  .bookmarktpop{
    width: 200px;
    display: none;
    padding: 5px;
    flex-direction: column;
    background-color: #ffffff;
    border-radius: 3px;
    position: absolute;
    left: -32px;
    z-index: 10;
  }
  .bookmarktpop .booksave1{
    display: flex;
    height: 40px;
    align-items: center;
   
    padding: 5px;
    margin-bottom: 5px;
  }
  .bookmarktpop .booksave1:hover{
    background-color: #f4f4f4;
    cursor: pointer;
  }
  .bookmarktpop .booksave1 img{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    padding: 0 !important;
    margin: 0  !important;
  }
  .bookmarktpop .booksave1 p{
    font-size: 14px;
    color: #494848;
    margin-bottom: 0 !important;
    padding: 0 5px 0 10px;
  }
 
  
  .bookmarktpop:after {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid transparent;
    border-bottom: 10px solid #fff;
    left: 29%;
    top: -18px;
  }
  .cfoot .frow .footicongroup img{
    cursor: pointer;
  } 