
.maingallery {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:50px;
}
.designgallery{
    padding: 100px 50px;
    background-color: #f4f4f4;
}
.designgallery .designheading h1{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    color: #494848;
    text-align: center;
    margin-bottom: 46px;
}
.designgallery .designsort{
    margin-bottom: 50px;
}
.designgallery .designsort form{
    height: 40px;
    display: flex;
    align-items: center;
}
.designgallery .designsort form label{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #828282;
    padding-right: 30px;
}
.designgallery .designsort form input{
    width: 250px;
    height: 38px;
    background-color: #FFFFFF !important;
    border: 1px solid #F2F2F2;
    padding : 5px 10px;
}
.maingallery .designpart {
display: grid;
}
.maingallery .designpart .mainimage {
  position: relative;
  width: inherit;
}
.maingallery .designpart .mainimage img{
    width:100%;
    height: 400px;
    object-fit: cover;
    object-position: center;
}
.maingallery .designpart .mainimage .imagehover{
    position: absolute;
    height: 400px;
    width: 100%;
    top: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.216) 28.74%, rgba(0, 0, 0, 0) 53.89%, rgba(0, 0, 0, 0.318) 77.4%, rgba(0, 0, 0, 0.6) 100%);
    backdrop-filter: blur(2px);
    display: none;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 20px;
}
.maingallery .designpart .mainimage:hover .imagehover{
    display: flex;
}
.maingallery .designpart .mainimage .imagehover .hoverhead{
    display: flex;
    justify-content: space-between;
    height: 48px;
    align-items: center;
}
.maingallery .designpart .mainimage .imagehover .infosvg{
    display: flex;
    align-items: center;
}
.maingallery .designpart .mainimage .imagehover .infosvg h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    padding-left: 5px;
}
.maingallery .designpart .mainimage .imagehover .hoverhead .hoverheadright{
    display: flex;
    align-items: center;
}
.maingallery .designpart .mainimage .imagehover .hoverhead .hoverheadright .bookmark{
    padding-left: 15px;
}
.maingallery .designpart .mainimage .imagehover .hoverbottom{
    display: flex;
    height: 45px;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
}
.maingallery .designpart .mainimage .imagehover .hoverbottom .hbottoml h4{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    color: #F2F2F2;
}
.maingallery .designpart .mainimage .imagehover .hoverbottom .hbottomr{
    display: flex;
}
.maingallery .designpart .mainimage .imagehover .hoverbottom .hbottomr .icons{
    padding-left: 10px;
}
.maingallery .designpart .designbottom{
    width: 100%;
    display: flex;
    background-color: #FFFFFF;
    height: 54px;
    align-items: center;
    padding: 0 20px;
    margin-top: -5px;
}
.maingallery .designpart .designbottom .dicons{
    display: flex;
    padding-right: 45px;
    align-items: center;
}
.maingallery .designpart .designbottom .dicons:last-child{
    padding-right: 0;
}
.maingallery .designpart .designbottom .dicons h6{
    font-family: 'Calibri';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #4F4F4F;
    padding-left: 8px;
    margin-bottom: 0%;
}
.popup-content {
    width: 100%;
    height: 100vh;
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    padding: 3vh 100px 0px 100px;
    background-color: rgba(0,0,0,0.6);
    z-index: 1;
}
  /* popupclose class */
  .close{
    position: fixed;
    top: 0;
    right: 30px;
    font-size: 42px;
    color: white;
    cursor: pointer;
    text-decoration: none;
}
