@media (max-width:769px) {
    .designgallery{
        padding: 100px 20px;
    }
    .designgallery .designsort{
        width: 100%;
    }
    .designgallery .designsort form input{
        width: 100%;
    }
    .designgallery .designsort form label{
        display: none;
    }
    .maingallery .designpart .mainimage .image_wrapper {
        width: 100%;
        height: auto;
        display: flex;
        flex-wrap: wrap;
    }
    .maingallery{
        grid-template-columns: 1fr;
    }
    .maingallery .designpart .designbottom{
        justify-content: space-between;
    }
    .maingallery .designpart .designbottom .dicons{
        padding-right: 0px;
    }
    .maingallery .designpart .designbottom .dicons img{
        width: 35%;
    }
    .maingallery .designpart .designbottom .dicons h6{
        font-size: 16px;
    }
    .maingallery .designpart .mainimage .imagehover{
        height: 300px;
    }
}